
.wrapper{
    border: solid;
    border-radius: 5px;
    box-shadow: 12px 12px 2px 1px rgb(46, 46, 46);
    padding: .5rem;
    background: blanchedalmond;
    max-height: 85vh;
    overflow-y: scroll;
    width: 90%;
}
  
  
  .new-item-form {
    display: flex;
    flex-direction: column;
    gap: 0.5rem;
    line-break: auto;
  
  }
  
  .form-row {
    display: flex;
    flex-direction: column;
    gap: 0.1rem;
    margin: .1rem;
  }
  
  
  .add-btn {
    background: hsl(200, 100%, 50%, 0.1);
    border: 1px solid hsl(200, 100%, 50%);
    color: hsl(200, 100%, 50%);
    padding: 0.25em 0.5em;
    border-radius: 0.25em;
    cursor: pointer;
    outline: none;
    width:50%;
    align-items: center;
  }
  
  .add-btn:hover,
  .add-btn:focus-visible {
    background: hsl(200, 100%, 50%, 0.2);
  }
  
  .del-btn {
    background: hsl(0, 100%, 40%, 0.1);
    border: 1px solid hsl(0, 100%, 40%);
    color: hsl(0, 100%, 40%);
    text-decoration: none;
    word-break: keep-all;
  }
  
  .del-btn:hover,
  .del-btn:focus-visible {
    background: hsl(0, 100%, 40%, 0.2);
  }
  
  .new-item-form input {
    outline: none;
    border: 1px solid hsl(200, 100%, 40%);
    border-radius: 0.25em;
    padding: 0.25em 0.5em;
  }
  
  .new-item-form input:focus {
    border: 1px solid hsl(200, 100%, 70%);
  }
  
  .date-picker{
    margin: .5rem;
  }
  
  .date-picker input{
    border: none;
  }
  
  .header {
    font-size: 1.5rem;
    margin-top: 1.5rem;
    margin-bottom: 0.5rem;
  }
  
  .list {
    margin: 0;
    padding: 0;
    margin-left: 1rem;
    list-style: none;
    word-break:break-all;
    line-break:normal;
  }
  
  .list li:has(input:checked) label {
    text-decoration: line-through;
  }
  
  .list {
    display: flex;
    flex-direction: column;
    gap: 0.3rem;
  }
  
  .list li {
    gap: 0.5rem;
    align-items: center;
    text-decoration: underline;
    display: grid;
    grid-template-columns:1fr auto auto ;
  
  }
  
  .list li label {
    display: flex;
    gap: 0.25rem;
    cursor: pointer;
    align-items: center;
  }
  
  .list li:has(input:focus-visible) label {
    outline: 1px solid hsl(200, 100%, 50%);
  }
  
  .list li input {
    outline: none;
    width: 0;
    height: 0;
    appearance: none;
    pointer-events: none;
    position: absolute;
  }
  
  .list li label::before {
    content: "";
    display: block;
    width: 0.9rem;
    height: 0.9rem;
    background: hsl(200, 100%, 90%);
    border-radius: 0.25em;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  
  .list li label:hover::before {
    background: hsl(200, 100%, 80%);
  }
  
  .list li:has(input:checked) label::before {
    content: "✔";
    text-decoration: line-through;
    font-size: 0.75rem;
    font-weight: bold;
  }
  
  .list li:has(input:checked) label:hover::before {
    background: hsl(200, 100%, 30%);
  }
  
  .bullet-list li {
    gap: 0.5rem;
    align-items: center;
    text-decoration: underline;
    display: grid;
    grid-template-columns:1fr auto auto ;
  
  }
  
  .bullet-list li label {
    display: flex;
    gap: 0.25rem;
    cursor: pointer;
    align-items: center;
  }
  
  .bullet-list li label::before { 
    content: "•";
    text-decoration: none;
  }
  
  .list-btn{
    font-size: 1.25rem;
    background: whitesmoke;
    color: black;
    border: solid 1px;
    margin: .5rem;
    box-shadow: 6px 6px 3px 1px rgb(46, 46, 46);
  }
  .list-btn:hover,
  .list-btn:focus-visible {
    background: hsla(0, 1%, 56%, 0.689);
  }
  
  .home-header{
    display: flex;
    justify-content: center;
  }
  
  .home-wrapper{
    border: solid;
    border-radius: 5px;
    box-shadow: 12px 12px 2px 1px rgb(46, 46, 46);
    padding: .5rem;
    background: blanchedalmond;
    max-height: 85vh;
    overflow-y: scroll;
    z-index: -1;
  }